import React, { useEffect, useState, Component } from "react";
import axios from "axios";
import { Link } from "react-router-dom";

import "bulma/css/bulma.min.css";
import "./style.scss";

function Car({ match }) {
  const [car, setCar] = useState({});
  const id = match.params.id;

  // useEffect(() => {
  //   axios({
  //     method: "GET",
  //     url: `http://carbikerental.std-953.ist.mospolytech.ru/api/cars/${id}/`,
  //   }).then((response) => {
  //     setCar(response.data);
  //   });
  // }, [id]);

  const [model, setModel] = useState({});


  useEffect(() => {
    axios
    .get(`http://carbikerental.std-953.ist.mospolytech.ru/api/cars/${id}/`)
    .then((response) => {
      setCar(response.data);
      console.log(car.car_model_id);
      console.log(response.data.car_model_id);
      return axios.get(`http://carbikerental.std-953.ist.mospolytech.ru/api/car_models/${response.data.car_model_id}/`)
    }).then((response) => {
      setModel(response.data);
    })
  }, [id]);

  

  // useEffect(() => {
  //   axios({
  //     method: "GET",
  //     url: `http://carbikerental.std-953.ist.mospolytech.ru/api/car_models/${car.car_model_id}/`,
  //   }).then((response) => {
  //     setModel(response.data);
  //   });
  // }, []);

  return (
    <div>
      <Link className="back" to="/cars">Назад</Link>
      <h1 className="title">{model.name}</h1>

      <div className="card-image">
        <img src={car.photo} alt="car image" />
      </div>

      <div class="params">
        <div class="name">Год выпуска</div> 
        <div class="filler"></div>
        <div class="val">{car.year}</div>
      </div>
      <div class="params">
        <div class="name">Цвет</div> 
        <div class="filler"></div>
        <div class="val">{car.color}</div>
      </div>
      <div class="params">
        <div class="name">Пробег</div> 
        <div class="filler"></div>
        <div class="val">{car.mileage} км</div>
      </div>

    </div>
  );
}

export default Car;
